.single-page {
	&__title {
		text-align: left;
		margin-bottom: rem(40px);
	}
	&__content {
		margin-bottom: 5rem;
	}
	&__columns {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&__column {
		margin-bottom: 2rem;
		width: calc(50% - 10px);
		float: none;
		height: fit-content;
		&--33 {
			width: calc(33% - 10px);
		}
	}
	&__block {
		margin-bottom: rem(50px);
	}
