	// 	transition: 0.3s all;
	// 	&:hover {
	// 		opacity: 0.7;
	// 	}
	// 	&:last-child {
	// 		margin-right: 0;
	// 	}
	// }
	&__discript {
		margin-bottom: 5rem;
	}
	&__preview {

	}
	&__preview-thumb {
		display: block;
		width: 100%;
		margin-bottom: rem(45px);
	}
	&__icon-title {
		display: flex;
		align-items: center;
		svg {
			display: block;
			height: 33px;
			width: 33px;
			margin-right: 10px;
		}
		span {

		}
	}
}

.preview {
	&__thumb {
		display: block;
		width: 100%;
		margin-bottom: rem(45px);
	}
}

// 10
.subtabs {
	margin-bottom: 2.5rem;
	&__tab {
		text-decoration: none;
		color: $blue;
		font-weight: bold;
		font-size: rem(18px);
		display: inline-block;
		margin-right: rem(32px);
		transition: 0.3s all;
		&:hover {
			opacity: 0.7;
		}
		&:last-child {
			margin-right: 0;
		}
	}
}