.friends {
	position: relative;
	padding-left: 90px;
	padding-right: 90px;
	&__slider {
		position: static;
	}
	&__slide {
		height: 110px;
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__slider-btn {
		position: absolute;
		top: 38px;
		&--prev {
			left: 0px;
			z-index: 2;
		}
		&--next {
			right: 0px;
			z-index: 2;
		}
	}
}